/**
 * Gets the current background theme ID from localStorage
 */
export const getCurrentBackgroundTheme = (): string => {
    if (typeof window === 'undefined') return 'default';
    return localStorage.getItem('userBackgroundTheme') || 'default';
};

/**
 * Gets the background color configuration by ID
 */
export const getBackgroundColorById = (id: string) => {
    return BACKGROUND_COLORS.find(color => color.id === id) || BACKGROUND_COLORS[0];
};

/**
 * Applies the background theme to the document body
 */
export const applyBackgroundTheme = (themeId: string) => {
    if (typeof window === 'undefined') return;

    const selectedColor = getBackgroundColorById(themeId);
    if (selectedColor) {
        document.body.style.background = selectedColor.gradient;
        document.body.style.backgroundAttachment = 'fixed';
        localStorage.setItem('userBackgroundTheme', themeId);
    }
};

/**
 * Loads and applies the saved background theme
 */
export const loadSavedBackgroundTheme = () => {
    const savedTheme = getCurrentBackgroundTheme();
    applyBackgroundTheme(savedTheme);
};

/**
 * Hook to initialize background theme on component mount
 */
export const useBackgroundTheme = () => {
    if (typeof window !== 'undefined') {
        // Apply immediately on load
        loadSavedBackgroundTheme();
    }
}; 